import { Button } from '@mui/material'
import { NavLink } from 'react-router-dom'
import { appPath } from '../../routesPath/appPath'

export const Navigation = () => {
   return (
      <ul className='nav-list'>
         <li className='nav-list__item'>
            <Button component={NavLink} to={appPath.PROFILE} variant="contained"  >
               
            </Button>
         </li>
         <li className='nav-list__item'>
            <Button component={NavLink} to={appPath.DIALOGS} variant="contained"  >
               
            </Button>
         </li>
         <li className='nav-list__item'>
            <Button component={NavLink} to={appPath.USERS} variant="contained"  >
               
            </Button>
         </li>
         <li className='nav-list__item'>
            <Button component={NavLink} to={appPath.NEWS} variant="contained"  >
               
            </Button>
         </li>
         <li className='nav-list__item'>
            <Button component={NavLink} to={appPath.MUSIC} variant="contained"  >
               
            </Button>
         </li>
         <li className='nav-list__item'>
            <Button component={NavLink} to={appPath.SETTINGS} variant="contained"  >
               
            </Button>
         </li>
      </ul>
   )
}